.profile__edit-userpick-button {
  padding: 0;
  border: 0;
  background: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  transition: background-color .2s ease;
}

.profile__edit-userpick-button:hover{
  background-image: url(../../../images/upEditButton.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-color: rgba(0,0,0,.8);
}